.pagination {
  display: flex;
  justify-content: center;
}

.pagination-list {
  display: flex;
  gap: 2px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;

  border: none;
  border-radius: 5px;
  cursor: pointer;

  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #000000;
  transition: transform 300ms linear, background-color 300ms linear,
    color 300ms linear;
}

.button__arrow,
.button__page--current {
  width: 40px;
  height: 40px;
  background-color: #f7f7f7;
}

.button__page, .dots {
  padding-left: 8px;
  padding-right: 8px;
  background-color: transparent;
}

.button__page--current {
  background-color: #ff6b08;
  color: #ffffff;
}

.button:hover, .button:focus {
  transform: scale(1.1);
  background-color: #ff6b08;
  color: #ffffff;
}

