.pagination {
  justify-content: center;
  display: flex;
}

.pagination-list {
  gap: 2px;
  display: flex;
}

.button {
  cursor: pointer;
  color: #000;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition: transform .3s linear, background-color .3s linear, color .3s linear;
  display: flex;
}

.button__arrow, .button__page--current {
  width: 40px;
  height: 40px;
  background-color: #f7f7f7;
}

.button__page, .dots {
  background-color: rgba(0, 0, 0, 0);
  padding-left: 8px;
  padding-right: 8px;
}

.button__page--current {
  color: #fff;
  background-color: #ff6b08;
}

.button:hover, .button:focus {
  color: #fff;
  background-color: #ff6b08;
  transform: scale(1.1);
}

